import React, { Component } from "react";
import PropTypes from 'prop-types';
import BigNumber from 'utils/BigNumber';
import { weiToRoundedEtherStr, weiToEtherStr } from 'utils/numUtils';

import selftokenIconCrop from "assets/img/selftoken_transparent_crop.png";


class Balances extends Component {
  render() {
    const { strings, ethBalanceBN, selfBalanceBN } = this.props;

    const roundedEthBalanceStr = weiToRoundedEtherStr(ethBalanceBN, 4);
    const roundedSelfBalanceStr = weiToRoundedEtherStr(selfBalanceBN, 2);
    const ethBalanceStr = weiToEtherStr(ethBalanceBN);
    const selfBalanceStr = weiToEtherStr(selfBalanceBN);

    // icon floats left and text floats right when screen width <= 767px
    // icon and text in the same line when screen width >= 768px
    return (
      <div className="row">
        <div className="col-md-6 text-center mb-2">
          <div className="h3 balance-container">
            <span className="h2 mb-0 mr-3 balance-icon-container">
              <i className="fab fa-ethereum"></i>
            </span>
            <span className="balance-text-container" title={ `${ethBalanceStr} ETH` }>
              { roundedEthBalanceStr } { strings.EN_ETH }
            </span>
          </div>
        </div>
        <div className="col-md-6 text-center mb-2">
          <div className="h3 balance-container">
            <span className="h2 mb-0 mr-3 balance-icon-container">
              <img
                src={selftokenIconCrop}
                alt="self-token-icon"
                style={{height: "1em"}}
              />
            </span>
            <span className="balance-text-container"  title={ `${selfBalanceStr} SELF` }>
              { roundedSelfBalanceStr } { strings.EN_SELF }
            </span>
          </div>
        </div>
      </div>
    );
  }
}

Balances.propTypes = {
  strings: PropTypes.object,
  selfBalanceBN: PropTypes.object,
  ethBalanceBN: PropTypes.object,
};

Balances.defaultProps = {
  strings: {},
  selfBalanceBN: new BigNumber(0),
  ethBalanceBN: new BigNumber(0),
};

export default Balances;
