import BigNumber from 'utils/BigNumber';


export const weiToEtherStr = (wei) => {
  // wei: BigNumber, BN, string
  return new BigNumber(wei.toString()).div(1e18).toString();
};

export const weiToRoundedEtherStr = (wei, dp=4) => {
  // wei: BigNumber, BN, string
  // dp (decimal place): number
  // return string
  return new BigNumber( new BigNumber(wei.toString()).div(1e18).toFixed(dp) ).toString();
};

export const ether = (num) => {
  // num: number / string
  // return BigNumber
  return new BigNumber(num).times(1e18);
}

export const fractionToPercentageStr = (fraction) => {
  // fraction: BigNumber
  return `${(new BigNumber(100)).div(fraction).toString()}%`;
};
